<template>

      <div class="col-md-6">
        <vuestic-widget v-if="ok" class="chart-widget" v-bind:headerText="title">
          <vuestic-chart :data="pieChartData" type="pie"></vuestic-chart>
        </vuestic-widget>
      </div>

</template>

<script>
import VuesticChart from '../vuestic-components/vuestic-chart/VuesticChart.vue'

export default {
  name: 'piechart',
  props:['url','title'],
  components: {
    VuesticChart
  },
  data: function () {
    return {
      url:'',
      title:'',
      json: null,
      ok: false,
      pieChartData: {
        labels: [],
        datasets: [{
          label: 'hhh',
          backgroundColor: [],
          data: []
        }]
      }
    }
  },
  created () {
    this.getdata()
  },
  methods: {
    getdata: function () {
      this.$http.get(this.url).then(
        function (res) {
          // 处理成功的结果
          console.log(this.pieChartData)
          this.json = res.data
          this.pieChartData.labels = this.json.labels
          this.pieChartData.datasets[0].label = this.json.datasets.label
          this.$set(this.pieChartData.datasets[0], 'backgroundColor', this.json.datasets.backgroundColor)
          this.$set(this.pieChartData.datasets[0], 'data', this.json.datasets.data)

          this.ok = true
          console.log(this.pieChartData)

        }, function (res) {
          console.log('fail json')
        }
      )
    }
  }
}
</script>

<style lang="scss">
.widget.chart-widget {
  .widget-body {
    height: 550px;
  }
}
</style>



